  left: 50%;
}

.lightsaber {
  width: 4px;
  height: 12px;
  background-color: #666;
  border-radius: 1px;
  position: absolute;
  bottom: 0;
}

.lightsaber.ls-left {
  left: 0;
}

.lightsaber.ls-right {
  right: 0;
}

.lightsaber:before {
  content: " ";
  width: 2px;
  height: 25px;
  max-height: 1px;
  transform-origin: top;
  background-color: #fff;
  border-radius: 1px;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  transform: rotateZ(180deg);
}

.lightsaber:after {
  content: " ";
  width: 2px;
  height: 2px;
  background-color: #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 4px;
  left: 1px;
}

.ls-particles {
  width: 1px;
  height: 5px;
  background-color: rgba(51, 51, 51, 0);
  position: absolute;
  top: 10px;
  left: 42px;
  transform: rotateZ(0deg);
}

.lightsaber.ls-green:before {
  animation: showlightgreen 2s ease-in-out 1s infinite;
}

.lightsaber.ls-red:before {
  animation: showlightred 2s ease-in-out 1s infinite;
}

.lightsaber.ls-left {
  animation: fightleft 2s ease-in-out 1s infinite;
}

.lightsaber.ls-right {
  animation: fightright 2s ease-in-out 1s infinite;
}

.ls-particles.ls-part-1 {
  animation: particles1 2s ease-out 1s infinite;
}

.ls-particles.ls-part-2 {
  animation: particles2 2s ease-out 1s infinite;
}

.ls-particles.ls-part-3 {
  animation: particles3 2s ease-out 1s infinite;
}

.ls-particles.ls-part-4 {
  animation: particles4 2s ease-out 1s infinite;
}

.ls-particles.ls-part-5 {
  animation: particles5 2s ease-out 1s infinite;
}

